import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, Tag } from 'lucide-react';
import api from '../lib/api';

const DiscountPopup = () => {
  const [popups, setPopups] = useState([]);
  const [currentPopup, setCurrentPopup] = useState(null);
  const [dismissed, setDismissed] = useState(new Set());

  useEffect(() => {
    fetchPopups();
  }, []);

  const fetchPopups = async () => {
    try {
      // Try public endpoint first
      let { data } = await api.get('/popups');
      console.log('Fetched popups from public endpoint:', data);
      
      // If that fails, try admin endpoint
      if (!data || data.length === 0) {
        try {
          const adminResponse = await api.get('/admin/popups');
          data = adminResponse.data;
          console.log('Fetched popups from admin endpoint:', data);
        } catch (adminError) {
          console.log('Admin endpoint also failed:', adminError);
        }
      }
      
      // Only show active popups
      const activePopups = data.filter(p => p.is_active);
      console.log('Active popups to display:', activePopups);
      setPopups(activePopups);
      if (activePopups.length > 0) {
        showNextPopup(activePopups);
      }
    } catch (error) {
      console.error('Failed to fetch popups:', error);
    }
  };

  const showNextPopup = (popupList) => {
    const availablePopups = popupList.filter(p => !dismissed.has(p.id));
    if (availablePopups.length > 0) {
      const popup = availablePopups[Math.floor(Math.random() * availablePopups.length)];
      setCurrentPopup(popup);
      
      // Auto-dismiss after duration ONLY for non-permanent popups
      if (popup.status !== 'permanent') {
        setTimeout(() => {
          handleDismiss(popup.id);
        }, popup.display_duration || 5000);
      }
    }
  };

  const handleDismiss = (popupId) => {
    // For permanent popups, allow them to show again later
    const popup = popups.find(p => p.id === popupId);
    if (popup && popup.status === 'permanent') {
      // Don't add permanent popups to dismissed set
      setCurrentPopup(null);
      // Show next popup after a delay for permanent popups
      setTimeout(() => {
        showNextPopup(popups);
      }, 10000); // Wait 10 seconds before showing next popup
    } else {
      // For active/inactive popups, dismiss permanently
      setDismissed(prev => new Set([...prev, popupId]));
      setCurrentPopup(null);
    }
  };

  return (
    <AnimatePresence>
      {currentPopup && (
        <motion.div
          initial={{ opacity: 0, x: 100, y: 100 }}
          animate={{ opacity: 1, x: 0, y: 0 }}
          exit={{ opacity: 0, x: 100 }}
          className="fixed bottom-6 right-6 z-50 max-w-sm"
          data-testid="discount-popup"
        >
          <div className="glass-panel rounded-2xl p-6 shadow-2xl border-2 border-purple-200/50">
            <button
              onClick={() => handleDismiss(currentPopup.id)}
              className="absolute top-3 right-3 p-1 rounded-full hover:bg-purple-100 transition-colors"
              data-testid="dismiss-popup-button"
            >
              <X className="w-4 h-4 text-slate-600" />
            </button>

            <div className="flex items-start space-x-4">
              <div className="p-3 rounded-full bg-gradient-to-r from-purple-600 to-fuchsia-500 animate-pulse-glow">
                <Tag className="w-6 h-6 text-white" />
              </div>
              
              <div className="flex-1">
                <h3 className="font-bold text-slate-900 mb-2 text-lg">
                  {currentPopup.title}
                </h3>
                <p className="text-slate-600 text-sm mb-3">
                  {currentPopup.message}
                </p>
                {currentPopup.discount_code && (
                  <div className="flex items-center space-x-2">
                    <code className="px-3 py-1.5 bg-purple-100 text-purple-700 rounded-lg font-mono text-sm font-semibold">
                      {currentPopup.discount_code}
                    </code>
                    <button
                      onClick={() => {
                        navigator.clipboard.writeText(currentPopup.discount_code);
                        // toast.success('Code copied!');
                      }}
                      className="text-xs text-purple-600 hover:text-purple-700 font-medium"
                    >
                      Copy
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default DiscountPopup;
