import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Shield, Clock, RefreshCw, CheckCircle2, XCircle } from 'lucide-react';
import toast from 'react-hot-toast';
import api from '../lib/api';

const VerifyCode = () => {
  const [code, setCode] = useState(['', '', '', '', '', '']);
  const [loading, setLoading] = useState(false);
  const [timeLeft, setTimeLeft] = useState(300); // 5 minutes in seconds
  const [isExpired, setIsExpired] = useState(false);
  const [isVerifying, setIsVerifying] = useState(false);
  const [verificationStatus, setVerificationStatus] = useState(null);
  const [canResend, setCanResend] = useState(true);
  const [countdown, setCountdown] = useState(300);
  const navigate = useNavigate();
  const inputRefs = useRef([]);

  // Get stored data from sessionStorage
  const storedEmail = sessionStorage.getItem('forgotPasswordEmail');
  const storedCode = sessionStorage.getItem('verificationCode');
  const expiresAt = sessionStorage.getItem('expiresAt');
  const userName = sessionStorage.getItem('userName');

  useEffect(() => {
    // Check if we have the required data
    if (!storedEmail || !storedCode || !expiresAt) {
      navigate('/forgot-password');
      return;
    }

    // Calculate initial time left
    const expirationTime = new Date(expiresAt).getTime();
    const currentTime = new Date().getTime();
    const initialTimeLeft = Math.max(0, Math.floor((expirationTime - currentTime) / 1000));
    setTimeLeft(initialTimeLeft);

    if (initialTimeLeft === 0) {
      setIsExpired(true);
    }
  }, [navigate, storedEmail, storedCode, expiresAt]);

  // Countdown timer for resend functionality
  useEffect(() => {
    if (countdown > 0 && !canResend) {
      const timer = setTimeout(() => {
        setCountdown(countdown - 1);
      }, 1000);
      return () => clearTimeout(timer);
    } else if (countdown === 0 && !canResend) {
      setCanResend(true);
    }
  }, [countdown, canResend]);

  useEffect(() => {
    if (timeLeft > 0 && !isExpired) {
      const timer = setTimeout(() => {
        setTimeLeft(timeLeft - 1);
      }, 1000);

      return () => clearTimeout(timer);
    } else if (timeLeft === 0 && !isExpired) {
      setIsExpired(true);
      toast.error('Verification code has expired. Please request a new one.');
    }
  }, [timeLeft, isExpired]);

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const handleInputChange = (index, value) => {
    if (value.length > 1) return;
    
    const newCode = [...code];
    newCode[index] = value;
    setCode(newCode);

    // Auto-focus next input
    if (value && index < 5) {
      inputRefs.current[index + 1].focus();
    }

    // Auto-verify when all fields are filled
    if (newCode.every(digit => digit !== '') && newCode.join('').length === 6) {
      handleVerify(newCode.join(''));
    }
  };

  const handleKeyDown = (index, e) => {
    if (e.key === 'Backspace' && !code[index] && index > 0) {
      inputRefs.current[index - 1].focus();
    }
  };

  const handlePaste = (e) => {
    e.preventDefault();
    const pastedData = e.clipboardData.getData('text').slice(0, 6);
    const newCode = pastedData.split('').map((char, index) => (index < 6 ? char : ''));
    setCode(newCode);
    
    // Focus the next empty input or the last one
    const nextEmptyIndex = newCode.findIndex(digit => digit === '');
    const focusIndex = nextEmptyIndex === -1 ? 5 : nextEmptyIndex;
    inputRefs.current[focusIndex].focus();

    // Auto-verify if complete
    if (newCode.every(digit => digit !== '') && newCode.join('').length === 6) {
      handleVerify(newCode.join(''));
    }
  };

  const handleVerify = async (codeToVerify = code.join('')) => {
    if (codeToVerify.length !== 6) {
      toast.error('Please enter all 6 digits');
      return;
    }

    setIsVerifying(true);
    setLoading(true);

    try {
      // Simulate verification delay for UX
      await new Promise(resolve => setTimeout(resolve, 1000));

      if (codeToVerify === storedCode) {
        setVerificationStatus('success');
        toast.success('Code verified successfully!');
        
        // Store verification success and redirect to reset password
        sessionStorage.setItem('verifiedEmail', storedEmail);
        sessionStorage.removeItem('verificationCode');
        sessionStorage.removeItem('expiresAt');
        
        setTimeout(() => {
          navigate('/reset-password');
        }, 1500);
      } else {
        setVerificationStatus('error');
        toast.error('Invalid verification code. Please try again.');
        
        // Clear the code after error
        setTimeout(() => {
          setCode(['', '', '', '', '', '']);
          inputRefs.current[0].focus();
          setVerificationStatus(null);
        }, 1500);
      }
    } catch (error) {
      console.error('Verification error:', error);
      toast.error('Verification failed. Please try again.');
      setVerificationStatus('error');
    } finally {
      setLoading(false);
      setIsVerifying(false);
    }
  };

  const handleResend = async () => {
    try {
      setLoading(true);
      
      // Generate new verification code
      const newVerificationCode = Math.floor(100000 + Math.random() * 900000).toString();
      const newExpiresAt = new Date(Date.now() + 5 * 60 * 1000).toISOString();
      
      // Get user name from email (simple fallback for reliability)
      const userName = storedEmail.split('@')[0];
      
      console.log('Sending verification code:', { storedEmail, userName, newVerificationCode });
      
      // Send new verification code to webhook
      const response = await fetch('https://hook.eu1.make.com/kuep0zwaltctbgihdpkvp2huytxgkney', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          user_email: storedEmail,
          user_name: userName,
          verification_code: newVerificationCode,
          expires_at: newExpiresAt
        })
      });

      console.log('Webhook response status:', response.status);
      
      if (response.ok) {
        // Update sessionStorage with new code
        sessionStorage.setItem('verificationCode', newVerificationCode);
        sessionStorage.setItem('expiresAt', newExpiresAt);
        
        // Show success message
        toast.success('New verification code sent successfully!', {
          duration: 4000,
        });
        
        // Reset countdown timer
        setCountdown(30);
        setCanResend(false);
        setTimeout(() => setCanResend(true), 30000); // Allow resend after 30 seconds
        
        // Clear any existing verification status
        setVerificationStatus(null);
      } else {
        console.error('Webhook failed with status:', response.status);
        // Even if webhook fails, still update the code locally so user can continue
        sessionStorage.setItem('verificationCode', newVerificationCode);
        sessionStorage.setItem('expiresAt', newExpiresAt);
        
        toast.success('New verification code generated! (Email service may be delayed)', {
          duration: 4000,
        });
        
        // Reset countdown timer
        setCountdown(30);
        setCanResend(false);
        setTimeout(() => setCanResend(true), 30000);
        
        setVerificationStatus(null);
      }
    } catch (error) {
      console.error('Error resending code:', error);
      
      // Even if there's an error, generate a new code so user can continue
      const newVerificationCode = Math.floor(100000 + Math.random() * 900000).toString();
      const newExpiresAt = new Date(Date.now() + 5 * 60 * 1000).toISOString();
      
      sessionStorage.setItem('verificationCode', newVerificationCode);
      sessionStorage.setItem('expiresAt', newExpiresAt);
      
      toast.success('New verification code generated! (Email service unavailable)', {
          duration: 4000,
        });
      
      // Reset countdown timer
      setCountdown(30);
      setCanResend(false);
      setTimeout(() => setCanResend(true), 30000);
      
      setVerificationStatus(null);
    } finally {
      setLoading(false);
    }
  };

  if (!storedEmail || !storedCode) {
    return null;
  }

  return (
    <div className="min-h-screen flex items-center justify-center px-4 py-12 relative overflow-hidden">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden">
        <motion.div
          animate={{
            opacity: [0.2, 0.5, 0.2],
            scale: [1, 1.05, 1],
          }}
          transition={{
            duration: 12,
            repeat: Infinity,
            ease: "easeInOut"
          }}
          className="absolute top-1/4 left-1/4 w-80 h-80 bg-purple-300/15 rounded-full blur-3xl"
        />
        <motion.div
          animate={{
            opacity: [0.3, 0.1, 0.3],
            scale: [1.05, 1, 1.05],
          }}
          transition={{
            duration: 15,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 3
          }}
          className="absolute bottom-1/4 right-1/4 w-64 h-64 bg-fuchsia-300/10 rounded-full blur-3xl"
        />
      </div>

      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0, scale: 0.95 }}
        transition={{ duration: 0.3, ease: "easeInOut" }}
        className="w-full max-w-md relative z-10"
      >
        <div className="glass-panel rounded-3xl p-8 shadow-2xl border border-purple-200/50">
          {/* Back button */}
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => navigate('/forgot-password')}
            className="mb-6 flex items-center gap-2 text-purple-600 hover:text-purple-700 transition-colors"
          >
            <ArrowLeft className="w-4 h-4" />
            <span className="text-sm font-medium">Back</span>
          </motion.button>

          <div className="text-center mb-8">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.2, type: "spring", stiffness: 200, damping: 15 }}
              className="inline-block mb-4"
            >
              <div className="w-16 h-16 bg-gradient-to-br from-purple-500 to-fuchsia-500 rounded-2xl flex items-center justify-center shadow-lg">
                <Shield className="w-8 h-8 text-white" />
              </div>
            </motion.div>
            
            <h1 className="text-3xl font-bold text-slate-900 mb-2">
              Enter Verification Code
            </h1>
            <p className="text-slate-600 mb-2">
              We've sent a 6-digit code to
            </p>
            <p className="font-medium text-purple-600">{storedEmail}</p>
          </div>

          {/* Timer */}
          <div className="mb-8">
            <motion.div
              animate={{ scale: timeLeft <= 60 ? [1, 1.05, 1] : 1 }}
              transition={{ repeat: timeLeft <= 60 ? Infinity : 0, duration: 1 }}
              className={`text-center p-4 rounded-2xl ${
                isExpired 
                  ? 'bg-red-50 border border-red-200' 
                  : timeLeft <= 60 
                    ? 'bg-orange-50 border border-orange-200'
                    : 'bg-green-50 border border-green-200'
              }`}
            >
              <div className="flex items-center justify-center gap-2">
                <Clock className={`w-5 h-5 ${
                  isExpired ? 'text-red-500' : timeLeft <= 60 ? 'text-orange-500' : 'text-green-500'
                }`} />
                <span className={`font-mono text-lg font-semibold ${
                  isExpired ? 'text-red-600' : timeLeft <= 60 ? 'text-orange-600' : 'text-green-600'
                }`}>
                  {isExpired ? 'Expired' : formatTime(timeLeft)}
                </span>
              </div>
              {!isExpired && (
                <p className="text-sm text-slate-500 mt-1">
                  Code expires in {timeLeft <= 60 ? 'less than a minute' : `${Math.floor(timeLeft / 60)} minutes`}
                </p>
              )}
            </motion.div>
          </div>

          {/* Code Input */}
          <div className="mb-8">
            <div className="flex justify-center gap-2">
              {code.map((digit, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.1 }}
                >
                  <input
                    ref={el => inputRefs.current[index] = el}
                    type="text"
                    value={digit}
                    onChange={(e) => handleInputChange(index, e.target.value)}
                    onKeyDown={(e) => handleKeyDown(index, e)}
                    onPaste={index === 0 ? handlePaste : undefined}
                    disabled={isExpired || loading}
                    maxLength={1}
                    className={`w-12 h-14 text-center text-xl font-bold rounded-xl border-2 transition-all ${
                      verificationStatus === 'success'
                        ? 'border-green-500 bg-green-50 text-green-600'
                        : verificationStatus === 'error'
                          ? 'border-red-500 bg-red-50 text-red-600'
                          : digit
                            ? 'border-purple-400 bg-purple-50 text-purple-600'
                            : 'border-purple-200 hover:border-purple-300 focus:border-purple-500 focus:ring-2 focus:ring-purple-200'
                    } outline-none disabled:opacity-50 disabled:cursor-not-allowed`}
                    placeholder="0"
                  />
                </motion.div>
              ))}
            </div>

            <AnimatePresence>
              {verificationStatus && (
                <motion.div
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  className={`mt-4 flex items-center justify-center gap-2 ${
                    verificationStatus === 'success' ? 'text-green-600' : 'text-red-600'
                  }`}
                >
                  {verificationStatus === 'success' ? (
                    <>
                      <CheckCircle2 className="w-5 h-5" />
                      <span className="font-medium">Code verified!</span>
                    </>
                  ) : (
                    <>
                      <XCircle className="w-5 h-5" />
                      <span className="font-medium">Invalid code</span>
                    </>
                  )}
                </motion.div>
              )}
            </AnimatePresence>
          </div>

          {/* Action Buttons */}
          <div className="space-y-3">
            <motion.button
              whileHover={{ scale: 1.02, boxShadow: "0 10px 40px rgba(107, 70, 255, 0.3)" }}
              whileTap={{ scale: 0.98 }}
              onClick={() => handleVerify()}
              disabled={loading || isExpired || code.join('').length !== 6}
              className="w-full py-3 rounded-xl bg-gradient-to-r from-purple-600 to-fuchsia-500 text-white font-semibold shadow-lg hover:shadow-purple-500/50 transition-all disabled:opacity-50 relative overflow-hidden"
            >
              <motion.div
                className="absolute inset-0 bg-gradient-to-r from-purple-400 to-fuchsia-400 opacity-0 hover:opacity-20 transition-opacity"
              />
              <span className="relative z-10">
                {isVerifying ? (
                  <span className="flex items-center justify-center gap-2">
                    <motion.div
                      animate={{ rotate: 360 }}
                      transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                      className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full"
                    />
                    Verifying...
                  </span>
                ) : (
                  'Verify Code'
                )}
              </span>
            </motion.button>

            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={handleResend}
              disabled={!canResend || loading}
              className="w-full py-3 rounded-xl border border-purple-200 text-purple-600 font-medium hover:bg-purple-50 transition-all disabled:opacity-50 flex items-center justify-center gap-2"
            >
              <RefreshCw className="w-4 h-4" />
              {loading ? 'Sending...' : !canResend && countdown > 0 ? `Resend in ${Math.ceil(countdown)}s` : (isExpired ? 'Request New Code' : "Resend Code")}
            </motion.button>
          </div>

          <div className="mt-8 pt-6 border-t border-purple-100">
            <p className="text-center text-sm text-slate-500">
              Need help? Contact{' '}
              <a href="mailto:blend4uwithhhezz@gmail.com" className="text-purple-600 hover:text-purple-700 font-medium transition-colors">
                support@blend4u.com
              </a>
            </p>
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default VerifyCode;
