import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Mail, Lock, User, Phone, Eye, EyeOff } from 'lucide-react';
import { useAuth } from '../context/AuthContext';
import toast from 'react-hot-toast';

const Login = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [fullName, setFullName] = useState('');
  const [phone, setPhone] = useState('');
  const [loading, setLoading] = useState(false);
  const { login, register, user } = useAuth();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const redirect = searchParams.get('redirect') || '/';
  const cameFromCheckout = redirect === '/checkout';

  // If user is already logged in, redirect immediately
  useEffect(() => {
    if (user) {
      navigate(redirect, { replace: true });
    }
  }, [user, navigate, redirect]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation(); // Extra prevention of event bubbling
    setLoading(true);
    
    try {
      if (isLogin) {
        await login(email, password);
        // The AuthContext already shows success toast, so we don't need another one
      } else {
        await register(email, password, fullName, phone);
        // The AuthContext already shows success toast, so we don't need another one
      }

      // Navigate after successful login/register
      navigate(redirect, { replace: true });
    } catch (error) {
      console.error('Auth error:', error);
      // The AuthContext already shows the error toast, so we don't need to show another one
      // Keep the user on the login page to try again
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center px-4 py-12">
      <motion.div 
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        exit={{ opacity: 0, scale: 0.95 }}
        transition={{ duration: 0.3, ease: "easeInOut" }}
        className="w-full max-w-md"
      >
        <div className="glass-panel rounded-3xl p-8 shadow-2xl">
          {cameFromCheckout && (
            <div className="mb-6 p-4 rounded-2xl bg-gradient-to-r from-purple-100 to-fuchsia-100 border border-purple-200/50">
              <p className="text-sm text-purple-700 font-medium text-center">
                Please login to continue with checkout
              </p>
            </div>
          )}

          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-slate-900 mb-2">
              {isLogin ? 'Welcome Back' : 'Create Account'}
            </h1>
            <p className="text-slate-600">{isLogin ? 'Sign in to your account' : 'Join Blend4u today'}</p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            {!isLogin && (
              <>
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Full Name</label>
                  <div className="relative">
                    <User className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                    <input
                      type="text"
                      value={fullName}
                      onChange={(e) => setFullName(e.target.value)}
                      className="w-full pl-10 pr-14 py-3 rounded-xl border border-purple-200 focus:border-purple-400 focus:ring-2 focus:ring-purple-200 outline-none transition-all"
                      placeholder="John Doe"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">Phone</label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                    <input
                      type="tel"
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      className="w-full pl-10 pr-14 py-3 rounded-xl border border-purple-200 focus:border-purple-400 focus:ring-2 focus:ring-purple-200 outline-none transition-all"
                      placeholder="+91 XXXXX XXXXX"
                    />
                  </div>
                </div>
              </>
            )}

            <div>
              <label className="block text-sm font-medium text-slate-700 mb-2">Email</label>
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="w-full pl-10 pr-14 py-3 rounded-xl border border-purple-200 focus:border-purple-400 focus:ring-2 focus:ring-purple-200 outline-none transition-all"
                  placeholder="you@example.com"
                />
              </div>
            </div>

            {/* Password Field */}
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-2">Password</label>
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />

                {/* NOTE: pr-14 ensures space for fixed-size eye button (w-10 + gap) */}
                <input
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="w-full pl-10 pr-14 py-3 rounded-xl border border-purple-200 focus:border-purple-400 focus:ring-2 focus:ring-purple-200 outline-none transition-all"
                  placeholder=""
                  autoComplete={isLogin ? 'current-password' : 'new-password'}
                />

                {/* Fixed-size eye button: w-10 h-10, absolutely positioned; icons stacked inside */}
                <button
                  type="button"
                  onClick={() => setShowPassword((s) => !s)}
                  aria-pressed={showPassword}
                  aria-label={showPassword ? 'Hide password' : 'Show password'}
                  className="absolute right-3 top-1/2 -translate-y-1/2 w-10 h-10 flex items-center justify-center rounded-md focus:outline-none"
                  style={{ pointerEvents: 'auto' }}
                >
                  {/* Both icons are in the same spot; we animate opacity/rotate so swapping won't change layout */}
                  <motion.span
                    initial={false}
                    animate={showPassword ? { opacity: 1, rotate: 0 } : { opacity: 0, rotate: -8 }}
                    transition={{ duration: 0.18 }}
                    className="absolute inset-0 flex items-center justify-center"
                    aria-hidden={!showPassword}
                  >
                    <EyeOff size={18} />
                  </motion.span>

                  <motion.span
                    initial={false}
                    animate={showPassword ? { opacity: 0, rotate: 8 } : { opacity: 1, rotate: 0 }}
                    transition={{ duration: 0.18 }}
                    className="absolute inset-0 flex items-center justify-center"
                    aria-hidden={showPassword}
                  >
                    <Eye size={18} />
                  </motion.span>
                </button>
              </div>
            </div>

            <motion.button
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              type="submit"
              disabled={loading}
              className="w-full py-3 rounded-xl bg-gradient-to-r from-purple-600 to-fuchsia-500 text-white font-semibold shadow-lg hover:shadow-purple-500/50 transition-shadow disabled:opacity-50"
            >
              {loading ? 'Please wait...' : isLogin ? 'Sign In' : 'Create Account'}
            </motion.button>
          </form>

          {isLogin && (
            <div className="mt-2 text-right">
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => navigate('/forgot-password')}
                className="text-purple-600 hover:text-purple-700 font-medium text-sm transition-colors"
              >
                Forgot Password?
              </motion.button>
            </div>
          )}

          <div className="mt-6 text-center">
            <motion.button
              whileHover={{ scale: 1.05, y: -2 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => setIsLogin(!isLogin)}
              className="text-purple-600 hover:text-purple-700 font-medium relative group"
            >
              <span className="relative z-10">
                {isLogin ? "Don't have an account? Sign up" : 'Already have an account? Sign in'}
              </span>
              <motion.div
                className="absolute inset-0 bg-purple-100 rounded-lg opacity-0 group-hover:opacity-20 transition-opacity"
              />
            </motion.button>
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default Login;
