import React, { useState } from 'react';
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { LayoutDashboard, Package, ShoppingBag, Tag, Users, ArrowLeft, Home, LogOut, Menu, X } from 'lucide-react';
import { useAuth } from '../../context/AuthContext';

const AdminLayout = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { isAdmin, logout } = useAuth();
  const [isLoggingOut, setIsLoggingOut] = React.useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const handleLogout = async () => {
    setIsLoggingOut(true);
    await logout();
    navigate('/');
  };

  const handleExit = () => {
    navigate('/');
  };

  if (!isAdmin) {
    navigate('/');
    return null;
  }

  const navItems = [
    { path: '/admin', icon: LayoutDashboard, label: 'Dashboard' },
    { path: '/admin/products', icon: Package, label: 'Products' },
    { path: '/admin/orders', icon: ShoppingBag, label: 'Orders' },
    { path: '/admin/discounts', icon: Tag, label: 'Discounts' }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-fuchsia-50" data-testid="admin-layout">
      {/* Mobile Header */}
      <div className="md:hidden bg-white/80 backdrop-blur-md border-b border-purple-100/50 sticky top-0 z-40">
        <div className="flex items-center justify-between p-4">
          <Link to="/" className="flex items-center space-x-2">
            <ArrowLeft className="w-5 h-5 text-slate-600" />
            <span className="text-lg font-bold bg-gradient-to-r from-purple-600 to-fuchsia-500 bg-clip-text text-transparent">
              Blend4u Admin
            </span>
          </Link>
          <button
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="p-2 rounded-lg hover:bg-purple-100 transition-colors"
          >
            {mobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
          </button>
        </div>
      </div>

      <div className="flex">
        {/* Desktop Sidebar */}
        <motion.aside
          initial={{ x: -300 }}
          animate={{ x: 0 }}
          className="hidden md:block w-64 min-h-screen glass-panel border-r border-purple-100/50 fixed left-0 top-0"
        >
          <div className="p-6">
            <Link to="/" className="flex items-center space-x-2 mb-8">
              <ArrowLeft className="w-5 h-5 text-slate-600" />
              <span className="text-lg font-bold bg-gradient-to-r from-purple-600 to-fuchsia-500 bg-clip-text text-transparent">
                Blend4u Admin
              </span>
            </Link>

            <nav className="space-y-2">
              {navItems.map((item) => {
                const isActive = location.pathname === item.path;
                return (
                  <Link
                    key={item.path}
                    to={item.path}
                    onClick={() => setMobileMenuOpen(false)}
                    className={`flex items-center space-x-3 px-4 py-3 rounded-xl transition-all ${
                      isActive
                        ? 'bg-gradient-to-r from-purple-600 to-fuchsia-500 text-white shadow-lg'
                        : 'text-slate-600 hover:bg-purple-100/50'
                    }`}
                    data-testid={`nav-${item.label.toLowerCase()}`}
                  >
                    <item.icon className="w-5 h-5" />
                    <span className="font-medium">{item.label}</span>
                  </Link>
                );
              })}
            </nav>

            {/* Action Buttons */}
            <div className="mt-8 pt-8 border-t border-purple-200/50 space-y-2">
              {/* Exit to Home Button */}
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.1 }}
              >
                <motion.button
                  whileHover={{ 
                    scale: 1.02,
                    y: -1,
                    boxShadow: "0 10px 25px rgba(107, 70, 255, 0.15)"
                  }}
                  whileTap={{ scale: 0.98 }}
                  onClick={handleExit}
                  className="w-full group relative overflow-hidden rounded-xl glass-panel border border-purple-200/50 hover:border-purple-300/70 transition-all duration-500"
                >
                  {/* Background Gradient Overlay */}
                  <div className="absolute inset-0 bg-gradient-to-r from-purple-600/5 via-fuchsia-500/5 to-purple-600/5 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                  
                  {/* Shimmer Effect */}
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent"
                    initial={{ x: -100 }}
                    whileHover={{ x: 100 }}
                    transition={{ duration: 0.8, ease: "easeInOut" }}
                  />
                  
                  <div className="relative flex items-center justify-center space-x-2 px-4 py-3">
                    <motion.div
                      animate={{ rotate: 0 }}
                      whileHover={{ 
                        rotate: [0, 15, -15, 0],
                        scale: 1.1
                      }}
                      transition={{ duration: 0.6, ease: "easeInOut" }}
                      className="p-2 rounded-full bg-gradient-to-br from-purple-500 to-fuchsia-500 text-white shadow-lg ring-2 ring-purple-200/50 hover:ring-purple-300/70 transition-all duration-300"
                    >
                      <Home className="w-4 h-4" />
                    </motion.div>
                    
                    <div className="flex-1 text-left">
                      <p className="font-semibold text-slate-800 group-hover:text-purple-700 transition-colors duration-300 text-sm">
                        Exit to Home
                      </p>
                    </div>
                    
                    <motion.div
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 0, x: -10 }}
                      whileHover={{ opacity: 1, x: 0 }}
                      transition={{ duration: 0.3 }}
                      className="p-2 rounded-full bg-purple-100 text-purple-600 ring-2 ring-purple-200/50 hover:ring-purple-300/70 transition-all duration-300"
                    >
                      <ArrowLeft className="w-3.5 h-3.5" />
                    </motion.div>
                  </div>
                </motion.button>
              </motion.div>

              {/* Logout Button */}
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.2 }}
              >
                <motion.button
                  whileHover={{ 
                    scale: 1.02,
                    y: -1,
                    boxShadow: "0 10px 25px rgba(239, 68, 68, 0.15)"
                  }}
                  whileTap={{ scale: 0.98 }}
                  onClick={handleLogout}
                  disabled={isLoggingOut}
                  className="w-full group relative overflow-hidden rounded-xl glass-panel border border-red-200/50 hover:border-red-300/70 transition-all duration-500 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {/* Background Gradient Overlay */}
                  <div className="absolute inset-0 bg-gradient-to-r from-red-500/5 via-pink-500/5 to-rose-500/5 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                  
                  {/* Shimmer Effect */}
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent"
                    initial={{ x: -100 }}
                    whileHover={{ x: 100 }}
                    transition={{ duration: 0.8, ease: "easeInOut" }}
                  />
                  
                  <div className="relative flex items-center justify-center space-x-2 px-4 py-3">
                    <motion.div
                      animate={{ rotate: 0 }}
                      whileHover={{ 
                        rotate: [0, -15, 15, 0],
                        scale: 1.1
                      }}
                      transition={{ duration: 0.6, ease: "easeInOut" }}
                      className="p-2 rounded-full bg-gradient-to-br from-red-500 to-pink-500 text-white shadow-lg ring-2 ring-red-200/50 hover:ring-red-300/70 transition-all duration-300"
                    >
                      <LogOut className="w-4 h-4" />
                    </motion.div>
                    
                    <div className="flex-1 text-left">
                      <p className="font-semibold text-slate-800 group-hover:text-red-700 transition-colors duration-300 text-sm">
                        {isLoggingOut ? (
                          <motion.div
                            animate={{ opacity: [1, 0.5, 1] }}
                            transition={{ duration: 1, repeat: Infinity }}
                            className="flex items-center space-x-2"
                          >
                            <div className="w-3.5 h-3.5 border-2 border-red-500 border-t-transparent rounded-full animate-spin" />
                            <span>Logging out...</span>
                          </motion.div>
                        ) : (
                          'Logout'
                        )}
                      </p>
                    </div>
                    
                    <motion.div
                      initial={{ opacity: 0, scale: 0.8 }}
                      animate={{ opacity: 0, scale: 0.8 }}
                      whileHover={{ opacity: 1, scale: 1 }}
                      transition={{ duration: 0.3 }}
                      className="p-2 rounded-full bg-red-100 text-red-600 ring-2 ring-red-200/50 hover:ring-red-300/70 transition-all duration-300"
                    >
                      <ArrowLeft className="w-3.5 h-3.5" />
                    </motion.div>
                  </div>
                </motion.button>
              </motion.div>
            </div>
          </div>
        </motion.aside>

        {/* Mobile Drawer */}
        <AnimatePresence>
          {mobileMenuOpen && (
            <>
              {/* Backdrop */}
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                onClick={() => setMobileMenuOpen(false)}
                className="md:hidden fixed inset-0 bg-black/50 z-40"
              />
              
              {/* Drawer */}
              <motion.aside
                initial={{ x: -300 }}
                animate={{ x: 0 }}
                exit={{ x: -300 }}
                transition={{ type: "spring", damping: 25, stiffness: 200 }}
                className="md:hidden fixed left-0 top-0 w-72 h-full glass-panel border-r border-purple-100/50 z-50 overflow-y-auto"
              >
                <div className="p-6">
                  <Link to="/" className="flex items-center space-x-2 mb-8">
                    <ArrowLeft className="w-5 h-5 text-slate-600" />
                    <span className="text-lg font-bold bg-gradient-to-r from-purple-600 to-fuchsia-500 bg-clip-text text-transparent">
                      Blend4u Admin
                    </span>
                  </Link>

                  <nav className="space-y-2">
                    {navItems.map((item) => {
                      const isActive = location.pathname === item.path;
                      return (
                        <Link
                          key={item.path}
                          to={item.path}
                          onClick={() => setMobileMenuOpen(false)}
                          className={`flex items-center space-x-3 px-4 py-3 rounded-xl transition-all ${
                            isActive
                              ? 'bg-gradient-to-r from-purple-600 to-fuchsia-500 text-white shadow-lg'
                              : 'text-slate-600 hover:bg-purple-100/50'
                          }`}
                          data-testid={`nav-${item.label.toLowerCase()}`}
                        >
                          <item.icon className="w-5 h-5" />
                          <span className="font-medium">{item.label}</span>
                        </Link>
                      );
                    })}
                  </nav>

                  {/* Mobile Action Buttons */}
                  <div className="mt-8 pt-8 border-t border-purple-200/50 space-y-2">
                    <motion.button
                      whileHover={{ scale: 1.02 }}
                      whileTap={{ scale: 0.98 }}
                      onClick={() => { handleExit(); setMobileMenuOpen(false); }}
                      className="w-full flex items-center justify-center space-x-2 px-4 py-3 rounded-xl bg-gradient-to-r from-purple-600 to-fuchsia-500 text-white font-medium shadow-lg hover:shadow-purple-500/50 transition-shadow"
                    >
                      <Home className="w-4 h-4" />
                      <span>Exit to Home</span>
                    </motion.button>

                    <motion.button
                      whileHover={{ scale: 1.02 }}
                      whileTap={{ scale: 0.98 }}
                      onClick={handleLogout}
                      disabled={isLoggingOut}
                      className="w-full flex items-center justify-center space-x-2 px-4 py-3 rounded-xl bg-gradient-to-r from-red-500 to-pink-500 text-white font-medium shadow-lg hover:shadow-red-500/50 transition-shadow disabled:opacity-50"
                    >
                      <LogOut className="w-4 h-4" />
                      <span>{isLoggingOut ? 'Logging out...' : 'Logout'}</span>
                    </motion.button>
                  </div>
                </div>
              </motion.aside>
            </>
          )}
        </AnimatePresence>

        {/* Main Content */}
        <main className="flex-1 md:ml-64 p-4 md:p-8">
          <Outlet />
        </main>
      </div>
    </div>
  );
};

export default AdminLayout;
